/*global kakao*/
import React, {useEffect} from "react";

const MapComponent = () => {


    useEffect(() => {
        const mapContainer = document.getElementById("map");
        let mapOption = {
            center: new kakao.maps.LatLng(37.50961746196608, 127.05947919675475), //  
            level: 3, //   
            mapTypeId: kakao.maps.MapTypeId.ROADMAP // 
        };
        //  
        const map = new kakao.maps.Map(mapContainer, mapOption);

        //    
        let marker = new kakao.maps.Marker({
            position: new kakao.maps.LatLng(37.50961746196608, 127.05947919675475), //  
            map: map //    
        });
        //     HTML  document element 
        var content = '<div class="customoverlay">' +
            '  <a href="https://map.kakao.com/?itemId=8229296" target="_blank">' +
            '    <span class="title"> </span>' +
            '  </a>' +
            '</div>';

//    
        var position = new kakao.maps.LatLng(37.50961746196608, 127.05947919675475);

//   
        var customOverlay = new kakao.maps.CustomOverlay({
            map: map,
            position: position,
            content: content,
            yAnchor: 1
        });


    }, []);


    return (
        <div className="mapDiv">
            <div id="map" style={{width: "80%", height: "300px", border: "2px solid #dbdbd9"}}/>
        </div>
    )
};

export default MapComponent;