import React from 'react';
import MapComponent from "../components/MapComponent";
import Fade from 'react-reveal/Fade';
import styled from "styled-components";


const Container = styled.div`
    align-items: left;
    text-align: left;
    border: 1px solid #ccc;
    margin-top: 2%;
    margin-left:9%;
    margin-right:9%;
    padding-top: 5%;
    padding-bottom: 5%;
    padding-left: 5%;
    padding-right: 5%;
`;

const LocationText = styled.div`
    padding-left: 10%;
    padding-right: 10%;
    font-size: 95%;
    font-family: 'NanumSquareAcb';
    color: #8A7376;
`;

const Line = styled.div`
    margin-top: 2%;
    margin-bottom: 3%;
    padding-left: 3%;
    padding-right: 3%;
    border-bottom: 1px solid #ccc;
`;


export default function MapContainer() {
    return (
        <div>
            <Fade bottom>
                <div className="titleDiv"> 
                </div>
                <MapComponent/>
                <div className="paddingTop">
                    <LocationText>   ( 3) </LocationText>
                </div>
                <Container>
                    <div className="mapDescLv1"></div>
                    <div className="mapDescLv2">   </div>
                    <Line/>
                    <div className="mapDescLv1"></div>
                    <div>
                        <div className="mapDescLv2"><span style={{color: "#2F9F27"}}>2</span>  5    7</div>
                        <div className="mapDescLv3">*    <span className="fontBold"></span>  .
                        </div>
                        <div className="mapDescLv2"><span style={{color: "#B0963C"}}>9</span>  7    10
                        </div>
                    </div>
                    <Line/>
                    <div className="mapDescLv1"></div>
                    <div>
                        <div className="mapDescLv2">  (  23-201)</div>
                        <div className="mapDescLv3"><span className="fontBold"></span> 146, 333, 341, 360, 740</div>
                        <div className="mapDescLv3"><span className="fontBold"></span> 3411 | <span
                            className="fontBold"></span> M6450
                        </div>
                        <div className="mapDescLv3"><span className="fontBold"></span> 1100, 1700, 2000, 2000-1, 7007,
                            500-2, 9303
                        </div>
                    </div>
                </Container>
            </Fade>
        </div>
    );
}