import React from 'react';
import styled from "styled-components";
import Fade from 'react-reveal/Fade';
import wedding_day from "../images/pictures/wedding_day_2.png";
import photo from "../images/pictures/main_picture_3.png";


const TitleImg = styled.img`
    padding-top: 6%;
    width: 86%;
    padding-bottom:0;
    display:block;
    padding-left: 7%;
    padding-right: 7%;
/*    filter: drop-shadow(1px 1px 2px rgba(0, 0, 0, .1));*/
`;

const MainContainer = styled.div`
    background-image: url(${photo});
    background-size: 100%;
    background-repeat: no-repeat;
`
const Background = styled.div`
    background-color: rgba( 255, 254, 249, 0);
    padding: 3% 1% 3% 1%;
`

const Contents = styled.div`
    padding-top: 85%;
    padding-bottom:10%;
    margin-top: 0;
    color: #938985;
`;
const Name = styled.div`
    font-family: 'NanumSquareAcb';
    padding-bottom: 2%;
`;
export default function TopContainer_2() {
    return (
        <div>
            <MainContainer>
                <Fade top>
                    <TitleImg src={wedding_day}/>
                </Fade>
                <Contents>
                    <Background>
                        <Fade bottom>
                            <Name>&nbsp;&nbsp;  &nbsp;&nbsp;</Name>
                            <div>2022 9 18   3</div>
                            <div style={{paddingTop: "1%"}}> </div>
                        </Fade>
                    </Background>
                </Contents>
            </MainContainer>

        </div>
    );
}