import React from 'react';
import styled from "styled-components";
import Fade from 'react-reveal/Fade';
import leaf_picture from "../images/pictures/leaf.png";
import tel from "../images/pictures/tel_pictogram.png";
import YuuPic from "../images/gallery/Img19.jpg";
import Img25 from "../images/gallery/Img25.jpg";

const ContentContainer = styled.div`
        padding-top: 15%;
        padding-bottom: 15%;
    `;

const TitleDiv = styled.div`
        padding-bottom: 10%;
        font-family: 'NanumSquareacB';
        font-size: 100%;
    `;

const BodyDiv = styled.div`
        padding: 2%;
    `;

const StyledSpan = styled.span`
        margin-left:2%;
        font-family: 'NanumSquareAceb';
    `;

const Picture = styled.img`
        width: 80%;
        padding-bottom: 5%;
        padding-top: 5%;
    `;

const TelPic = styled.img`
        width: 12%;
        vertical-align: middle;
        padding-left: 3%;
    `;

const PersonalPhoto = styled.img`
        width: 40%;
        padding-top: 10%;
        padding-right: 1%;
    `;
export default function ContentsContainer() {
    return (<div>
            <ContentContainer>
                <Fade bottom>
                    <Picture src={leaf_picture}/>
                </Fade>

                <Fade bottom cascade>
                    <TitleDiv>.</TitleDiv>
                    <BodyDiv>9  </BodyDiv>
                    <BodyDiv>.</BodyDiv>
                    <BodyDiv></BodyDiv>
                    <BodyDiv> .</BodyDiv>
                </Fade>
                <Fade bottom>
                    <Picture src={leaf_picture}/>
                </Fade>
                <Fade bottom>
                </Fade>
                <Fade bottom cascade>
                    <div className="nameDiv"><span>  </span><span
                        className="smallFont"> </span><StyledSpan></StyledSpan></div>
                    <div className="nameDiv"><span>  </span><span
                        className="smallFont"> </span><StyledSpan></StyledSpan></div>
                </Fade>

                <Fade bottom>
                    <div>
                        <PersonalPhoto src={Img25}/>
                        <PersonalPhoto src={YuuPic}/>
                    </div>
                    <div className="telSection">
                        <div className="telDiv"><span className="fontRegular"></span> <a
                            href='tel:010-6510-9229'><TelPic src={tel}/></a></div>
                        <div className="telDiv"><span className="fontRegular"></span> <a
                            href='tel:010-6510-9229'><TelPic src={tel}/></a></div>
                    </div>
                </Fade>
            </ContentContainer>
        </div>
    )
        ;
}