import * as React from 'react';
import {styled, ThemeProvider, createTheme} from '@mui/material/styles';
import MuiAccordion from '@mui/material/Accordion';
import MuiAccordionSummary from '@mui/material/AccordionSummary';
import MuiAccordionDetails from '@mui/material/AccordionDetails';
import Typography from '@mui/material/Typography';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';


const theme = createTheme();

theme.typography.font = {
    fontFamily: 'NanumSquareAcr', color: '#6e6a67', textAlign: 'left',
};

theme.typography.fontDetail = {
    fontFamily: 'NanumSquareAcl', fontSize: '90%', color: '#6e6a67', textAlign: 'left',
};


const Accordion = styled((props) => (<MuiAccordion disableGutters elevation={0} square {...props} />))(({theme}) => ({
    // border: `1px solid ${theme.palette.divider}`,
    marginLeft: '15%', marginRight: '15%', '&:not(:last-child)': {
        borderBottom: 0,
    }, '&:before': {
        display: 'none',
    }, marginBottom: '2%'
}));

const AccordionSummary = styled((props) => (<MuiAccordionSummary
        {...props}
    />))(({}) => ({
    backgroundColor: '#F8F5EF', '& .MuiAccordionSummary-content': {
        marginLeft: '5%'
    },
}));

const AccordionDetails = styled(MuiAccordionDetails)(({theme}) => ({
    borderBottom: '1px solid rgba(0, 0, 0, .125)', paddingLeft: '7%', paddingRight: '7%', paddingTop: '5%'
}));

export default function AccordionComponent() {
    return (<div>
            <ThemeProvider theme={theme}>
                <Accordion>
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon/>}
                        aria-controls="panel1a-content"
                        id="panel1a-header"
                    >
                        <Typography variant="font">
                               </Typography>

                    </AccordionSummary>
                    <AccordionDetails>
                        <Typography variant="fontDetail">
                            <div>  </div>
                        </Typography>
                    </AccordionDetails>
                </Accordion>
                <Accordion>
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon/>}
                        aria-controls="panel2a-content"
                        id="panel2a-header"
                    >
                        <Typography variant="font">
                               </Typography>

                    </AccordionSummary>
                    <AccordionDetails>
                        <Typography variant="fontDetail">
                            <div className="accountDiv"> 1002-756-471511
                                <div style={{paddingTop: "1%"}} className="fontRegular">  </div>
                            </div>
                            <div className="accountDiv">
                                 1002-756-471511
                                <div style={{paddingTop: "1%"}} className="fontRegular">  </div>
                            </div>

                            <div className="accountDiv"> 1002-756-471511
                                <div
                                    style={{paddingTop: "1%"}} className="fontRegular">  </div>
                            </div>
                        </Typography>
                    </AccordionDetails>
                </Accordion>
            </ThemeProvider>
        </div>);
}